import React from "react";
import { useSelector } from "react-redux";
import ContentCard from "../../UI/Card/ContentCard";
import BudgetsContent from "./BudgetsContent";


import './Budgets.scss';

function Budgets() {
  const darkmode = useSelector((state) => state.darkmode.darkmode);
  const tempResources = [
    {
      id: "rs-1",
      resource: "MQV K1",
      budget_used: "20000",
      allocation: "500000",
      used_color: "#DE5215",
      remaining_color: "#FCD5D5",
    },
    {
      id: "rs-2",
      resource: "MQV K5",
      budget_used: 300000,
      allocation: 500000,
      used_color: "#247931",
      remaining_color: "#A7F8B1",
    },
  ];
  const transformResourced = tempResources.map((resource) => ({
    id: resource.id,
    resource: resource.resource,
    budget_used: resource.budget_used,
    allocation: resource.allocation,
    used_color: resource.used_color,
    remaining_color: resource.remaining_color
  }));

  //const ctx = useContext(ThemeContext);

    return (
      <div className="main_content">
        <ContentCard
          className={` ${darkmode ? "dark_bg" : "white_bg"
          } budgets_container`}
        >
          <div className="container_header_wrap">
            <h4 className="page_header">Budgets</h4>
          </div>
          <BudgetsContent items={transformResourced} />
        </ContentCard>
      </div>
    );
}

export default Budgets;