// Importing modules
import React, { useEffect, useState } from "react";
import {
  Link,
  useNavigate
} from "react-router-dom";

import { useDispatch } from "react-redux";
import AlertCard from "../../UI/MessageBox/AlertCard";
import Button from "../../UI/Button/Button";
import { authActions } from "../../../store/auth-slice";




function LoginForm() {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const [identity, setIdentity] = useState("");
  const [password, setPassword] = useState("");
  const [identityValidation, setIdentityValidation] = useState(false);
  const [passwordValidation, setPasswordValidation] = useState(false);
  const [identityTouched, setIdentityTouched] = useState(false);
  const [passwordTouched, setPasswordTouched] = useState(false);

  const [error, setError] = useState(null);
  const [isSubmitting, setIsSubmitting] = useState(false);

  

  useEffect(() => {
    if (identity.trim().length > 0) {
      setIdentityValidation(true);
    } else {
      setIdentityValidation(false);
    }
    //console.log("identity valid: " + identityValidation);
    if (password.trim().length > 0) {
      setPasswordValidation(true);
    } else {
      setPasswordValidation(false);
    }
    //console.log("pwd valid: " + passwordValidation);
    // validate form logic
    // if (identity.trim().length === 7 || identity.includes('@')) {
    //    setIdentityValidation(true);
    //  } 


    // add timeout here to reduce time of request every changing
}, [identity, password, identityValidation, passwordValidation]);
  

  const identityChangeHandler = (event) => {
    setIdentity(event.target.value);
    setIdentityTouched(true);
  };
  const passwordChangeHandler = (event) => {
    setPassword(event.target.value);
    setPasswordTouched(true);
  };

  

  const clearError = () => {
    setError(null);
    
  };
  
  const loginHandler = async (event) => {
    event.preventDefault();
    setIsSubmitting(true);

    // validate input data
    if (identity.trim().length === 0) {
      setError("Identity must not be empty!");
      setIdentityValidation(false);
      setIsSubmitting(false);
      return;
    }
    //console.log("identity valid: " + identityValidation);
    if (password.trim().length === 0) {
      setError("Password must not be empty!");
      setPasswordValidation(false);
      setIsSubmitting(false);
      return;
    }
    //console.log("pwd valid: " + passwordValidation);
    // validate form logic
    // if (identity.trim().length === 7 || identity.includes('@')) {
    //    setIdentityValidation(true);
    //  }

    const authData = {
      identity: identity,
      secret: password,
    };
    // submit hook
    //submit({authData: authData}, {method: "post", action: "/login"});

    // send request to backend API
    // console.log("current host: " + process.env.REACT_APP_API_ENDPOINT);
    const login_url = process.env.REACT_APP_API_ENDPOINT + "/login";
    
    try {
      // send HTTP request
      const response = await fetch(login_url, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(authData),
      });

      // console.log("Response:");
      // console.log(response);
      
      // check errors
      if (!response.ok) {
        setError("Internal Server Error!");
        setIsSubmitting(false);
        return;

      } else {
        const resData = await response.json();
        console.log("Response Data:");
        console.log(resData);
        // successfully login
        dispatch(authActions.logged_in());

        const access_token = resData.access_token;
        //console.log(token);
        if (access_token) {
          dispatch(authActions.save_token({ access_token }));
          // forced reset password
          const forcedResetPwd = resData.force_secret_reset;
          // console.log("is reset:");
          // console.log(forcedResetPwd);
          if (forcedResetPwd) {
            // forced to reset password
            setIsSubmitting(false);
            dispatch(authActions.enable_reset());
            return navigate("/forced_reset_password");
          } else {
            // set expiration
            const expiration = new Date();
            //expiration.setMinutes(expiration.getMinutes() + 15); // + 15 minutes in future
            expiration.setSeconds(expiration.getSeconds() + (15 * 60 - 10)); // convert 15 minutes to seconds - 10 seconds
            localStorage.setItem("expiration", expiration.toISOString());

            setIsSubmitting(false);

            // redirect to Status page
            return navigate("/status");
          }
        }
      }

    } catch (error) {
      console.log("Catch errors:");
      console.log(error);
      setError("Internal Server Error! Please try again later.");
      setIsSubmitting(false);
      return;
    }
  };


  

  return (
      <form id="LoginForm" method="POST" onSubmit={loginHandler}>
          {error && (
              <AlertCard variant="danger" onClear={clearError}>
                  {error}
              </AlertCard>
          )}
          <div className="form-field my-3">
              <label htmlFor="identity">Identity *</label>
              <input
                  type="text"
                  id="identity"
                  name="identity"
                  value={identity}
                  onChange={identityChangeHandler}
                  className={`${
                      identityTouched && !identityValidation
                          ? "form-control invalid_input"
                          : "form-control"
                  }`}
              />
              
          </div>
          <div className="form-field my-3">
              <label htmlFor="password">Password *</label>
              <input
                  type="password"
                  id="password"
                  name="password"
                  value={password}
                  onChange={passwordChangeHandler}
                  className={`${
                      passwordTouched && !passwordValidation
                          ? "form-control invalid_input"
                          : "form-control"
                  }`}
              />
              
              <div className="mt-2 d-flex justify-content-end">
                  <Link
                      to="/forgot_password"
                      className="dashboard_link text_color_grey fs-7"
                      title="Forgot Password"
                  >
                      Forgot your password?
                  </Link>
              </div>
          </div>

          <div className="text-center mt-4">
              <Button type="submit" className="login_btn" disabled={!identityValidation || !passwordValidation}>
                  {isSubmitting ? "Logging..." : "Login"}
              </Button>
          </div>
      </form>
  );
}

export default LoginForm;

