import { createSlice } from "@reduxjs/toolkit";


const initialAuthState = {
    isAuthenticated: false,
    access_token: null,
  isForcedReset: true,
};
const authSlice = createSlice({
  name: "authentication",
  initialState: initialAuthState,
  reducers: {
      logged_in(state) { 
          state.isAuthenticated = true;          
          localStorage.setItem("isLoggedIn", state.isAuthenticated);          
      },
      logout(state) {
          state.isAuthenticated = false;
          state.access_token = null;
          localStorage.removeItem("isLoggedIn");
          localStorage.removeItem("token");
          localStorage.removeItem("isReset");
          localStorage.removeItem("expiration");
      },
      save_token(state, action) {
          state.access_token = action.payload.access_token;
          localStorage.setItem("token", state.access_token);
      },
      enable_reset(state) {
          state.isForcedReset = true;
          localStorage.setItem("isReset", state.isForcedReset);
      },
      disable_reset(state) {
          state.isForcedReset = false;
          localStorage.setItem("isReset", state.isForcedReset);
      }
  },
});

export const authActions = authSlice.actions;
export default authSlice.reducer;