import React from "react";
import { useRouteLoaderData } from "react-router-dom";
import useHttp from "../../../hooks/use-http";


function ActivedTokenListItem(props) {
  const access_token = useRouteLoaderData("home");
  const { isLoading, httpErr, sendRequest: revokeToken } = useHttp();
  const revokeTokenHandler = async () => {
    const proceed = window.confirm("Are you sure to revoke this token?");
    if (proceed) {

      const forwardResponse = (response) => {
        props.onRevoke(response, props.tokenName);
        console.log(response);
      }

      const revoke_url = process.env.REACT_APP_API_ENDPOINT + "/tokens";
      const requestData = {
        token_name: props.tokenName,
      };
      revokeToken({
        url: revoke_url,
        method: "DELETE",
        headers: {
          Authorization: "Bearer " + access_token,
          "Content-Type": "application/json",
        },
        body: requestData,
      },
        forwardResponse
      );
    }
  };

  return (
    <li className="token_item_row" id={props.tokenName}>
      <div className="token_column token_name">{props.tokenName}</div>
      <div className="token_column token_actions">
        <button className="token_btn " onClick={revokeTokenHandler}>
          <span className="button_icon delete_icon"></span>
          {isLoading ? "Revoking..." : "Revoke"}
        </button>
      </div>
    </li>
  );
}

export default ActivedTokenListItem;