import React, { useState, useEffect } from "react";
import {
  useLoaderData,
  defer,
  useRouteLoaderData,
} from "react-router-dom";
import { useSelector } from "react-redux";
import ContentCard from "../../UI/Card/ContentCard";
import BlankCard from "../../UI/Card/BlankCard";
import AlertCard from "../../UI/MessageBox/AlertCard";
import TokensList from "./TokensList";
import NewToken from "./NewToken";
import useHttp from "../../../hooks/use-http";

import SuccessfullyToken from "./SuccessfullyToken";
import { getAuthToken } from "../../utils/auth";
//import { getDarkmode } from "../../utils/theme";

import './Tokens.scss';

function Tokens() {
  const darkmode = useSelector((state) => state.darkmode.darkmode);
  const access_token = useRouteLoaderData("home");
  const { userLimits } = useLoaderData();

  const [tokens, setTokens] = useState(null);
  const [newToken, setNewToken] = useState(null);
  const [message, setMessage] = useState(null);
  

  const { isLoading, httpErr, sendRequest: fetchTokens } = useHttp();
  

  
  useEffect(() => {
    // convert tokens from database to frontend object
    const transformedTokens = (tokensObj) => {
      const loadedTokens = [];
      if (tokensObj && tokensObj.tokens.length > 0) {
        const tokensList = tokensObj.tokens;
        for (let i = 0; i < tokensList.length; i++) {
          loadedTokens.push({
            token_name: tokensList[i].token_name,
            revoked: tokensList[i].revoked,
            revoke_reason: tokensList[i].revoke_reason,
          });
        }
        setTokens(loadedTokens);
      }
    };
    fetchTokens(
      {
        url: process.env.REACT_APP_API_ENDPOINT + "/tokens",
        headers: {
          Authorization: "Bearer " + access_token,
          "Content-Type": "application/json",
        },
      },
      transformedTokens
    );
  }, [fetchTokens, access_token]);

  const addTokenHandler = (newToken) => {
    if (newToken) {
      setNewToken(newToken);    
    }
    // fetch new tokens list
    const updateTokens = (newTokensList) => {
      // console.log("new tokens:");
      // console.log(newTokensList.tokens);
      setTokens((prevTokens) => newTokensList.tokens);
    }
    fetchTokens(
      {
        url: process.env.REACT_APP_API_ENDPOINT + "/tokens",
        method: "GET",
        headers: {
          Authorization: "Bearer " + access_token,
          "Content-Type": "application/json",
        },
      },
      updateTokens
    );    
    

  };

  const clearNewTokenHandler = () => {
    setNewToken(null);
  };
  const revokeActionHandler = (response, revokedToken) => {
    if (response.message) {
      setMessage(response.message);
      const newTokens = tokens.filter(
        (token) => token.token_name !== revokedToken
      );
      setTokens((prevTokens) => newTokens);
    }
    if (response.error_message) {
      setMessage(response.error_message);
    }
    
  };
  
  // console.log("current tokens: ");
  // console.log(tokens);

  return (
    <React.Fragment>
      <div className="main_content">
        {httpErr && <AlertCard variant="danger">{httpErr}</AlertCard>}
        {!newToken && (
          <NewToken onAddToken={addTokenHandler} userLimits={userLimits} />
        )}
        {newToken && (
          <SuccessfullyToken token={newToken} onClear={clearNewTokenHandler} />
        )}

        {isLoading && (
          <BlankCard>
            <p style={{ textAlign: "center" }}>Loading...</p>
          </BlankCard>
        )}

        <div>
          {(!tokens ||
            tokens.length === 0) && (
              <BlankCard>
                <p style={{ textAlign: "center" }}>Tokens is empty.</p>
              </BlankCard>
            )}

          {tokens && tokens.length > 0 && (
            <div>
              <ContentCard
                className={`${
                  darkmode ? "dark_bg" : "white_bg"
                } tokens_container`}
              >
                {message && <AlertCard variant="danger">{message}</AlertCard>}

                <div className="listTokens_container">
                  <div className="tokenContainer_header_wrap">
                    <h4 className="mb-5 page_header">All Tokens</h4>
                  </div>
                  <div className="tokensList_container">
                    <TokensList
                      tokens={tokens}
                      onRevokeAction={revokeActionHandler}
                    />
                  </div>
                </div>
              </ContentCard>
            </div>
          )}
        </div>
      </div>
    </React.Fragment>
  );
}

export default Tokens;

