async function loadUserTokenLimits() {
  const access_token = getAuthToken();
  const fetch_url = process.env.REACT_APP_API_ENDPOINT + "/tokens/user_limits"; 
  const response = await fetch(fetch_url, {
    method: "GET",
    headers: {
      Authorization: "Bearer " + access_token,
      "Content-Type": "application/json",
    },
  });

  if (!response.ok) {
    // throw new Response(JSON.stringify({ message: "Could not fetch token limits!" }), {
    //   status: response.status,
    // });
    return response;
  } else {
    const data = await response.json();
    // console.log("responsed data:");
    // console.log(data);
    
    return data;
  }
}

export async function loader() {
  return defer({
    //tokens: await loaderTokensList(),
    userLimits: await loadUserTokenLimits(),
  });
}

