import { createSlice } from "@reduxjs/toolkit";
import { getDarkmode } from "../components/utils/theme";

const initialDarkmodeState = getDarkmode();
//console.log("initial darkmode:" + initialDarkmodeState);

const darkmodeSlice = createSlice({
  name: "darkmode",
  initialState: { darkmode: initialDarkmodeState },
  reducers: {
    toggle(state, action) {
      state.darkmode = action.payload;
      localStorage.setItem("darkmode", state.darkmode);
    },
  },
});

export const darkmodeAction = darkmodeSlice.actions;
export default darkmodeSlice.reducer;