import React from "react";
import { NavLink, useRouteLoaderData } from "react-router-dom";

import Logout from "../../SecuredPages/Logout";


const UserNavigation = () => {
  const token = useRouteLoaderData("home");

    return (
      <div className="user_acc">
        <ul className="mainNav userNav">
          {token && (
            <li className="nav-item ">
              <NavLink
                to="settings"
                title="Account Settings"
                className="nav-link "
                {...({ isActive }) => (isActive ? "active" : undefined)}
                end
              >
                <span className="link_icon settings_icon"></span>
                Settings
              </NavLink>
            </li>
          )}

          <li className="nav-item ">
            <Logout />
          </li>
        </ul>
      </div>
    );
}

export default UserNavigation;