import React, {useState} from "react";
import BlankCard from "../../UI/Card/BlankCard";
import { Link, Form, useActionData } from "react-router-dom";
import FormMessage from "../../UI/MessageBox/AlertCard";
import Button from "../../UI/Button/Button";

const AccountSettings = () => {
    //const data = useActionData();
    const data = {
      identity: "",
      password: "",
      lastLoggedIn: 'dd.mm.YYYY'
    };
    const [password, setPassword] = useState(data.password);
    let replacedPwd = '';
    if (data === null) {
        return <p>Could not load data of current user!</p>
    }

    const passwordChangeHandler = (event) => {
      setPassword(event.target.value);
    };

    const editPasswordHandler = () => {};

    if (password.trim().length > 0) {
        replacedPwd = password.replace('*' * password.trim().length);
    }

  return (
    <BlankCard>
      <Form id="AccountSettingForm" method="post">
        {data && data.message && <FormMessage message={data.message} />}
        <div className="form-field my-4">
          <label htmlFor="identity">Email Address</label>
          <input
            type="text"
            id="identity"
            name="identity"
            value={data.identity}
            disabled
            className="form-control"
          />
        </div>
        <div className="form-field my-4">
          <label htmlFor="password">Password </label>
          <input
            type="password"
            id="password"
            name="password"
            value={replacedPwd}
            onChange={passwordChangeHandler}
            className="form-control"
          />
          {/** 
          <button
            onClick={editPasswordHandler}
            className="text_color_grey fs-7 editPwd_btn"
            title="Change Password"
          >
            <span className="edit_icon"></span> Change Password
          </button>
           
          */}
        </div>
        <div className="form-field my-4">
          <label htmlFor="identity">Last logged in</label>
          <input
            type="text"
            id="last_logged_in"
            name="last_logged_in"
            value={data.lastLoggedIn}
            disabled
            className="form-control"
          />
        </div>
        <div className=" mt-5">
          <Button type="submit" className="save_btn">
            Save
          </Button>
        </div>
      </Form>
    </BlankCard>
  );
};

export default AccountSettings;
