import { createSlice } from "@reduxjs/toolkit";


const initialTokenState = {
  activatedTokens: [],
  revokedTokens: []
};
const tokenSlice = createSlice({
  name: "token",
  initialState: initialTokenState,
  reducers: {

    addTokenItem(state, action) {
      const newItem = action.payload;
      
      state.activatedTokens = state.activatedTokens.unshift({
        id: newItem.id,
        token_name: newItem.token_name,
        revoke: false,
      });
    },
    revokeTokenItem(state, action) {
      const revokedId = action.payload;
      state.activatedTokens = state.activatedTokens.filter(item => item.id !== revokedId);
      state.revokedTokens = state.revokedTokens.filter(
        (item) => item.id === revokedId
      );
    },
    replaceActivatedTokens(state, action) {
      state.activatedTokens = action.payload.activatedTokens;
    },
    replaceRevokedTokens(state, action) {
      state.revokedTokens = action.payload.revokedTokens;
    },
 
  },
});

const sendTokenData = (tokenData) => {

}

export const tokenActions = tokenSlice.actions;
export default tokenSlice.reducer;