 
// Importing modules
import React, { lazy, Suspense } from "react";
//import { useSelector } from "react-redux";
import { createBrowserRouter, RouterProvider, redirect } from "react-router-dom";

import RootLayout from "./components/SecuredPages/RootLayout";
import DefaultLayout from "./components/SecuredPages/DefaultLayout";
import Login from "./components/UnSecuredPages/Login/Login";
import Logout, { action as logoutAction } from "./components/SecuredPages/Logout";
import ForceResetLayout from "./components/SecuredPages/ForcedResetLayout";
import ForcedResetPassword from "./components/SecuredPages/ForcedReset/ForcedResetPassword";
import ForgotPassword from "./components/UnSecuredPages/ForgotPassword/ForgotPassword";
import Blocked from "./components/UnSecuredPages/Blocked";
import ErrorPage from "./components/UnSecuredPages/ErrorsHandling/Error";
import DefaultErrorPage from "./components/UnSecuredPages/ErrorsHandling/DefaultError";
import StatusRoot from "./components/SecuredPages/Status/StatusRoot";
import Status, {
  loader as loadPointers,
} from "./components/SecuredPages/Status/Status";
import JobsRoot from "./components/SecuredPages/Jobs/JobsRoot";
import Jobs, { loader as jobsLoader } from "./components/SecuredPages/Jobs/Jobs";
import JobDetail, { loader as jobDetailLoader } from "./components/SecuredPages/Jobs/JobDetail";
import Budgets from "./components/SecuredPages/Budgets/Budgets";
import ResourcesRoot from "./components/SecuredPages/Resources/ResourcesRoot"; 
import Resources, { loader as resourcesLoader } from "./components/SecuredPages/Resources/Resources";
import ResourceDetail from "./components/SecuredPages/Resources/ResourceDetail";
import Settings from "./components/SecuredPages/Settings/Settings";
import Maintainance from "./components/UnSecuredPages/Maintainance";
import {
  authenticationLoader,
  checkAuthentication,
  tokenLoader,
  checkTokenLoader
} from "./components/utils/auth";

import TokensRootLayout from "./components/SecuredPages/Tokens/TokensRoot";
import Tokens, {
  loader as tokensLoader,
} from "./components/SecuredPages/Tokens/Tokens";


import './App.scss';

// lazy loading
//const Tokens = lazy(() => import("./components/Pages/Tokens/Tokens"));
//const Jobs = lazy(() => import("./components/Pages/Jobs/Jobs"));


function App() {
  

	const router = createBrowserRouter([
        {
            path: "/",
            element: <RootLayout />,
            id: "home",
            loader: tokenLoader,
            children: [
                {
                    path: "forced_reset_password",
                    element: <ForceResetLayout />,
                    errorElement: <ErrorPage />,
                    loader: checkTokenLoader,
                    children: [
                        {
                            index: true,
                            element: <ForcedResetPassword />,
                        },
                    ],
                },

                {
                    path: "status",
                    element: <StatusRoot />,
                    errorElement: <ErrorPage />,
                    id: "status",
                    loader: checkTokenLoader,
                    children: [
                        {
                            index: true,
                            element: <Status />,
                            //loader: loadPointers,
                        },
                    ],
                },
                {
                    path: "tokens",
                    element: <TokensRootLayout />,
                    errorElement: <ErrorPage />,
                    loader: checkTokenLoader,
                    children: [
                        {
                            index: true,
                            element: <Tokens />,
                            loader: tokensLoader,
                        },
                    ],
                },
                {
                    path: "jobs",
                    element: <JobsRoot />,
                    errorElement: <ErrorPage />,
                    loader: checkTokenLoader,
                    children: [
                        {
                            index: true,
                            element: <Jobs />,
                            loader: jobsLoader,
                        },
                        {
                            path: ":jobId",
                            id: "job-detail",
                            children: [
                                {
                                    index: true,
                                    element: <JobDetail />,
                                    loader: jobDetailLoader,
                                },
                            ],
                        },
                    ],
                },
                {
                    path: "budgets",
                    element: <Budgets />,
                    errorElement: <ErrorPage />,
                    loader: checkTokenLoader,
                },
                {
                    path: "resources",
                    element: <ResourcesRoot />,
                    errorElement: <ErrorPage />,
                    loader: checkTokenLoader,
                    children: [
                        {
                            index: true,
                            element: <Resources />,
                            loader: resourcesLoader,
                        },
                        {
                            path: ":resourceId",
                            id: "resource-detail",
                            children: [
                                {
                                    index: true,
                                    element: <ResourceDetail />,
                                },
                            ],
                        },
                    ],
                },
                {
                    path: "settings",
                    element: <Settings />,
                    errorElement: <ErrorPage />,
                    loader: checkTokenLoader,
                },
            ],
        },
        {
            path: "/",
            element: <DefaultLayout />,
            errorElement: <DefaultErrorPage />,
            children: [
                { index: true, element: <Login /> },
                {
                    path: "login",
                    element: <Login />,
                    //action: loginAction,
                },
                {
                    path: "logout",
                    element: <Logout />,
                    action: logoutAction,
                },
                {
                    path: "blocked",
                    element: <Blocked />,
                },
                {
                    path: "forgot_password",
                    element: <ForgotPassword />,
                },
                {
                    path: "maintainance",
                    element: <Maintainance />
                },
                {
                    path: "error",
                    element: <DefaultErrorPage />
                }
            ],
        },
    ]);
 

  	return <RouterProvider router={router} />;  
        
}

export default App;
