import React, { useState } from "react";
//import { useSelector } from "react-redux";
import { useRouteLoaderData } from "react-router-dom";
import Card from "../../UI/Card/ContentCard";
import AlertCard from "../../UI/MessageBox/AlertCard";
import CreateTokenForm from "./CreateTokenForm";
import { getDarkmode } from "../../utils/theme";
import useHttp from "../../../hooks/use-http";

const NewToken = (props) => {
  
  const darkmode = getDarkmode();
  const access_token = useRouteLoaderData("home");
  const [toggleFormShow, setToggleFormShow] = useState(false);
  const [toggleButtonShow, setToggleButtonShow] = useState(true);
  const [error, setError] = useState(null);

  const toggle_create_token_form = () => {
    setToggleFormShow(true);
    setToggleButtonShow(false);
  };
  const hideFormHandler = (isShowed) => {
    setToggleFormShow(isShowed);
    setToggleButtonShow(true);
  };

  if (props.isShow === "true") {
    setToggleFormShow(true);
    setToggleButtonShow(false);
  }

  const { isLoading, httpErr, sendRequest: addTokenRequest } = useHttp();

  const createToken = (responseData) => {
    // console.log(responseData);
    if (responseData.token_data) {
    
      // pass up created token to parent component
      props.onAddToken(responseData.token_data);

    }
    if (responseData.error_message) {
      setError(responseData.error_message);
      //props.onAddToken(null, responseData.error_message);
    }
  }

  // fetch tokens
  //const access_token = getAuthToken();
  const tokens_url = process.env.REACT_APP_API_ENDPOINT + "/tokens"; 

  const submitTokenHandler = async (requestTokenData) => {
    
    // processing fetch data
    addTokenRequest(
      {
        url: tokens_url,
        method: "POST",
        headers: {
          Authorization: "Bearer " + access_token,
          "Content-Type": "application/json",
        },
        body: requestTokenData,
      },
      createToken
    );

  };

  

  return (
    <Card className={`${darkmode ? "dark_bg" : "white_bg"} tokens_container`}>
      <div className="createToken_container">
        {error && <AlertCard variant="danger">{error}</AlertCard>}
        <div className="text-center">
          {toggleButtonShow && (
            <button
              className="dashboard_btn create_token"
              onClick={toggle_create_token_form}
            >
              Create New Token
            </button>
          )}
        </div>

        {toggleFormShow && (
          <CreateTokenForm
            onShowing={hideFormHandler}
            onSubmit={submitTokenHandler}
            loading={isLoading}
            userLimits={props.userLimits}
          />
        )}
      </div>
    </Card>
  );
}

export default NewToken;

