import React, {useState, useContext} from "react";
import ContentCard from "../../UI/Card/ContentCard";
import BlankCard from "../../UI/Card/BlankCard";
import Tab from "react-bootstrap/Tab";
import Tabs from "react-bootstrap/Tabs";
import AccountSettings from "./AccountSettings";
import AccessibilitiesSettings from "./AccessibilitiesSettings";


import { getDarkmode } from "../../utils/theme";

import "./Settings.scss";

function Settings() {
	const [key, setKey] = useState("account_settings");
  const darkmode = getDarkmode();

  
	
  return (
    <React.Fragment>

        <div className="main_content">
          <ContentCard
            className={`${darkmode ? "dark_bg" : "white_bg"
              } settings_container`}
          >
            <Tabs
              id="settings"
              activeKey={key}
              onSelect={(k) => setKey(k)}
              className=""
            >
              <Tab eventKey="account_settings" title="Account Settings">
                <AccountSettings />
              </Tab>
              <Tab
                eventKey="accessibilities_settings"
                title="Accessibilities Settings"
              >
                <AccessibilitiesSettings />
              </Tab>
            </Tabs>
          </ContentCard>
        </div>

    </React.Fragment>
  );
}

export default Settings;


