import React, { useEffect, useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import CSSTransition from "react-transition-group/CSSTransition";
import Navbar from "react-bootstrap/Navbar";
import MainNavigation from "../MainNavigation/MainNavigation";
import { darkmodeAction } from "../../../store/darkmode-slice";
import { getDarkmode } from "../../utils/theme";

function TopBar() {
  const dispatch = useDispatch();
  //const darkmode = useSelector((state) => state.darkmode.darkmode);
  // const currentDarkmodeStatus = getDarkmode();
  // console.log(currentDarkmodeStatus);
	const [toggleMenu, setToggleMenu] = useState(false);
  const [darkmode, setDarkmode] = useState(false);
  

	const options = {
		day: '2-digit',
		month: 'short',
		year: 'numeric'
	};
	const today = new Date().toLocaleString('de-DE', options);
	
  const toggleMenuHandler = () => {
    setToggleMenu(prevState => !prevState);
  }
  //console.log("current darkmode:" + darkmode);
  const toggleDarkmodeHandler = () => {
    
    setDarkmode(prevDarkmode => !prevDarkmode);
    //console.log("new darkmode:" + darkmode);
  }
  dispatch(darkmodeAction.toggle(darkmode));


    return (
      <div className={`topbar ${darkmode ? "dark_bg" : "white_bg"}`}>
        <div className="topbar_element date">
          <div className="calendar_icon"></div>
          <span id="topbar_today_date">{today}</span>
        </div>
  

        <div className="topbar_element search_form_wrap"></div>

        <div className="topbar_element accessibilities">
          <button type="button"
            className="accessibility_btn dark_mode_toggle"
            id="darkmode_toggle"
            onClick={toggleDarkmodeHandler}
          ></button>
          <button type="button"
            className="accessibility_btn font_size_toggle"
            id="fs_toggle"
          ></button>
        </div>
        <div className="topbar_element navbar_toggle">
          <button
            type="button"
            aria-controls="main_navigation"
            aria-label="Toggle Navigation"
            className={`navbar_toggler ${!toggleMenu ? "collapsed" : ""}`}
            onClick={toggleMenuHandler}
          >
            <span className="toggler_bar bar_1"></span>
            <span className="toggler_bar bar_2"></span>
            <span className="toggler_bar bar_3"></span>
          </button>
          <CSSTransition in={toggleMenu} timeout={200} classNames="mainNavbar">
            <MainNavigation id="main_navigation" />
          </CSSTransition>
        </div>
      </div>
    );
								
}

export default TopBar;