import { redirect } from "react-router-dom";


export function getTokenDuration() {
  const savedExpirarionDate = localStorage.getItem("expiration");
  const expirationDate = new Date(savedExpirarionDate); // convert to Date format
  const now = new Date(); // get current moment
  const duration = expirationDate.getTime() - now.getTime();
  return duration;
}
/*
 * IsLoggedIn
 */
export function getAuthentication() {
    const isLoggedIn = localStorage.getItem("isLoggedIn");
    if (!isLoggedIn) {
      return null;
    }
    
    return isLoggedIn;
    
}

export function authenticationLoader() {
  return getAuthentication();
}

export function checkAuthentication() {
  const isLoggedIn = getAuthentication();
  if (!isLoggedIn) {
    return redirect("/login");
  }
  return null;
}

/*
 * Token
 */
export function getAuthToken() {
  const token = localStorage.getItem("token");
    if (!token) {
        return null;
    }
    const tokenDuration = getTokenDuration();
    if (tokenDuration < 0) {
      return "EXPIRED";
    }
    return token;
}

export function tokenLoader() {
    return getAuthToken();
}

export function checkTokenLoader() {
  const token = getAuthToken();
  
  if (!token) {
    return redirect("/login");
  }

  return token;
}


/*
 * IsForcedReset
 */


export function getForcedResetStatus() {
    const isReset = localStorage.getItem("isReset");
    if (isReset || isReset === "1") {
        return null;
    }
    return isReset;
}





