import React, { useState } from "react";
import Button from "../../UI/Button/Button";
import ContentCard from "../../UI/Card/ContentCard";
import NotificationCard from "../../UI/MessageBox/NotificationCard";

const SuccessfullyToken = (props) => {
  
  console.log(props.token);
    const [isCopied, setIsCopied] = useState(false);
    const copyHandler = () => {
        navigator.clipboard.writeText(props.token.token_value);
        //console.log("text is copied");
        setIsCopied(true);
    }
  
  const onClickHandler = () => {
    props.onClear();
  }
    return (
      <ContentCard>
        <div className="tokenContainer_header_wrap">
          <h4 className="mb-5 page_header">New token created successfully</h4>
        </div>
        <NotificationCard variant="primary">
          A new token has been successfully created. Please note: Token hash is
          displayed <b>only once</b>.
        </NotificationCard>

        <div className="token_detail">
          <div className="token_detail_item">Token Value:</div>
          <div className="token_detail_value token_hash">
            {props.token.token_value}
          </div>
          
          <button className="copy_btn" onClick={copyHandler}>
            {isCopied ? "Copied" : "Copy"}
          </button>
          
        </div>
        <div className="token_detail token_detail_btn_wrap">
          <Button onClick={onClickHandler} className="my-4">
            OK
          </Button>
        </div>
      </ContentCard>
    );
}

export default SuccessfullyToken;