import React from "react";
import { redirect, useNavigate, Link } from "react-router-dom";
import JobDetail from "./JobDetail";

const JobListItem = (props) => {
  const job = props.job;
  // console.log("job: ");
  // console.log(job);
  let status_bg = "";

  if (job.status === "RUNNING") {
    status_bg = "running_bg";
  }
  if (job.status === "PENDING" || job.status === "WAITING") {
    status_bg = "pending_bg";
  }
  if (job.status === "CANCELLED") {
    status_bg = "canceled_bg";
  }
  if (job.status === "COMPLETE") {
    status_bg = "complete_bg";
  }

  return (
    <li className="job_item_row" key={job.id} id={`${job.id}`}>
      <Link to={`/jobs/${job.id}`}>
        <div className={`job_column job_view ${status_bg}`}>
          <span className="view_icon"></span>
        </div>
        <div className={`job_column job_id ${status_bg}`}>{job.id}</div>
        <div className={`job_column job_status ${status_bg}`}>{job.status}</div>
        <div className={`job_column job_shots ${status_bg}`}>{job.shots}</div>
        <div className={`job_column job_circuit ${status_bg}`}>
          {job.circuit}
        </div>
        <div className={`job_column job_circuit_format ${status_bg}`}>
          {job.circuit_format}
        </div>
        <div className={`job_column job_target ${status_bg}`}>
          {job.target_specification}
        </div>
        <div className={`job_column job_note ${status_bg}`}>{job.note}</div>
      </Link>
    </li>
  );
};


export default JobListItem;